import React from "react";

interface committeeProps {
  name: string;
  role: string;
  bio: string;
  img?: string;
}

function CommitteeCard(props: committeeProps) {
  return (
    <>
      <img src={props.img} alt={props.name} />
      <h2>{props.name}</h2>
      <h3>{props.role}</h3>
      <p>{props.bio}</p>
    </>
  );
}

export default CommitteeCard;
