import React, { useState } from "react";
import CommitteeCard from "./CommitteeCard";
import "./CommitteeCards.css";
import rahul from "../images/rahul.jpg";
import mark from "../images/mark.jpg";

function CommitteeCards() {
  const data = [
    {
      id: 1,
      name: "Mark",
      role: "President",
      img: mark,
      bio: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ab delectus illo consequuntur unde beatae omnis exercitationem tempora. Vel soluta, quam, sit commodi et cumque suscipit repellendus recusandae quos quas ducimus nisi facere voluptas, dignissimos veritatis? Tempora soluta reprehenderit sit rem dolorum dolorem obcaecati esse provident ea possimus laboriosam recusandae, distinctio temporibus fugit cupiditate perspiciatis. Cupiditate ipsam tempora doloribus expedita sit rem? Assumenda voluptates voluptatem, mollitia veniam iste labore alias suscipit dolore delectus maxime amet ex porro ullam, iusto rem culpa.",
    },
    {
      id: 2,
      name: "Rahul Avula",
      role: "Treasurer",
      img: rahul,
      bio: " Lorem ipsum dolor sit, amet consectetur adipisicing elit. Odio eum optio inventore veniam non molestiae dolorum nostrum ipsum quis dolorem?",
    },
  ];

  const [index, setIndex] = useState(0);

  const nextCard = () => {
    if (index == data.length - 1) {
      setIndex(0);
    } else {
      setIndex((prev) => (prev += 1));
    }
  };

  const prevCard = () => {
    if (index == 0) {
      setIndex(data.length - 1);
    } else {
      setIndex((prev) => (prev -= 1));
    }
  };

  return (
    <div className="container">
      <div className="main-committee-card">
        <CommitteeCard
          key={data[index].id}
          name={data[index].name}
          role={data[index].role}
          bio={data[index].bio}
          img={data[index].img}
        />
        <div className="commitee-btns">
          <button onClick={prevCard}>
            <i className="fas fa-chevron-left"></i>
          </button>
          <button onClick={nextCard}>
            <i className="fas fa-chevron-right"></i>
          </button>
          <div className="img"></div>
        </div>
      </div>
    </div>
  );
}

export default CommitteeCards;
